.randomchar {
    display: flex;
    justify-content: space-between;
    // align-items: center;
    &__block {
        padding: 35px 35px 35px 40px;
        background: #FFF;
        box-shadow: 5px 5px 20px 0px rgba(0, 0, 0, 0.25);
        width: 550px;
        display: flex;
    }
    &__info {
        margin-left: 30px;
    }
    &__name {
        color: #000;
        font-size: 22px;
        font-weight: 700;
    }
    &__descr {
        color: #000;
        font-size: 14px;
        font-weight: 400;
    }
    &__img {
        width: 180px;
        height: 180px; 
        object-fit: cover;
    }
    &__buttons {
        a:nth-child(1) {
            margin-right: 30px;
        }
    }
    &__static {
        padding: 35px 40px;
        width:550px;
        background: #232222;
        box-shadow: 5px 5px 20px 0px rgba(0, 0, 0, 0.25);
        position: relative;
    }
    &__title {
        color: #FFF;
        font-family: Roboto Condensed;
        font-size: 24px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        &:nth-child(2) {
            margin-top: 33px;
            margin-bottom: 52px;
        }
    }
    &__decoration {
        position: absolute;
        bottom: 20px;
        right: -40px;
    }
}