@import '../style/variables.scss';


.app {
    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__title {
        line-height: 37px;
        font-size: 28px;
        font-weight: 700;
        transition: all 0.4s;
        span {
            color: #9F0013;
        }
        &:hover {
            transform: translateY(-5px);
        }
    }
    &__menu {
        ul {
            display: flex;
            justify-content: space-between;
            font-weight: bold;
            font-size: 24px;
            line-height: 32px;
            li {
                margin: 0 8px;
                transition: all 0.4s;
                &:hover {
                    color: $main-color;
                }
            }
        }
    }
}