.char {
    &__info {
        width: 425px;
        margin-left: 25px;
        background: #FFF;
        box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.25);
        padding: 25px;
        position: relative;
        z-index: 5;
        height: 823px;
        &-name {
            font-weight: bold;
            font-size: 22px;
            line-height: 29px;
            text-transform: uppercase;
        }
    }
    &__basics {
        display: flex;
        img {
            width: 150px;
            height: 150px;
            object-fit: cover;
            margin-right: 25px;
        }
    }
    &__buttons {
        margin-top: 35px;
        a:nth-child(2) {
            margin-top: 10px;
        }
    }
    &__descr {
        margin-top: 15px;
        color: #000;
        font-size: 14px;
        font-weight: 400;
    }
    &__comics {
        font-weight: bold;
        font-size: 18px;
        line-height: 24px;
        margin-top: 10px;
        &-list {
            position: relative;
            margin-top: 10px;
        }
        &-item {
            width: 100%;
            padding: 0px 10px;
            line-height: 25px;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            margin-top: 10px;
        }
    }
}