.char {
    &__wrapper {
        display: flex;
        flex-wrap: wrap;
        gap: 30px 25px;
    }
    &__content {
        margin-top: 50px;
        display: flex;
    }
    &__list {
        width: 650px;
    }
    &__item {
        width: 200px;
        height: 333px;
        background-color: #232222;
        box-shadow: 5px 5px 20px 0px rgba(0, 0, 0, 0.25);
        cursor: pointer;
        transition: 0.3s transform;
        padding: 15px;
        img {
            width: 200px; 
            object-fit: cover;
            height: 200px;
            transform: translate(-15px, -15px);
        }
        &_selected {
            filter: drop-shadow(0px 5px 20px #9F0013);
            transform: translateY(-8px);
        }
    }
    &__name {
            font-weight: bold;
            font-size: 22px;
            line-height: 29px;
            text-transform: uppercase;
            color: #fff;
    }
}